  margin-right: 10px;
}
.tour__details-level::before {
  position: absolute;
  content: "";
  left: 0;
  bottom: 4px;
  width: 80px;
  height: 14px;
  background-image: url("../img/sprite.svg#star-empty");
  background-repeat: repeat;
  background-size: 16px 14px;
}
.tour__details-level::after {
  position: absolute;
  content: "";
  left: 0;
  bottom: 4px;
  height: 14px;
  background-image: url("../img/sprite.svg#star-filled");
  background-repeat: repeat;
  background-size: 16px 14px;
}
.tour__details-level--two::after {
  width: 32px;
}
.tour__details-level--three::after {
  width: 48px;
}
.tour__details-level--four::after {
  width: 64px;
}
