    bottom: 6px;
    width: 73px;
    height: 13px;
    background-size: 13px 12px;
    background-repeat: space;
  }
  .tour__details-level::after {
    bottom: 6px;
    height: 13px;
    background-size: 13px 12px;
    background-repeat: space;
  }
  .tour__details-level--two::after {
    width: 28px;
  }
